.jobcontainer {
    background-color: #FAF1EA;
    width: 100%;
    height: auto;
    padding: 70px;
    display: flex;
}


.leftContainer {
    /* background-color: green; */
    width: 65%;
    height: auto;
}

.company {
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 100%;
    height: auto;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    border-radius: 15px;
    /* padding-bottom: 10px; */
    padding: 10px 10px;
}

.companydescription {
    padding: 10px;
    height: 70%;
    width: 100%;
    /* background-color: brown; */
    display: flex;

}

.leftcompanydescripton {
    height: 100%;
    width: 60%;
    /* background-color: honeydew; */
    display: flex;
    flex-direction: column;
    gap: 5px;

}

.leftcompanydescripton>h1 {
    font-size: 16px;
    font-weight: 900;
}

.dreview {
    height: 20px;
    width: 20px;
    display: flex;
    /* align-items: center; */
    justify-content: center;
    color: yellow;
    font-size: 15px;
    /* background-color: aqua; */
}

.ownername {
    display: flex;
    /* justify-content: center; */
    /* align-items: center; */
    /* background-color: chartreuse; */
    width: 200px;
    height: auto;
    gap: 5px;
    /* font-size: small; */
}

.ownername>h1 {
    font-size: 14px;
    font-weight: 900;
}

.ownername>p {
    font-size: 12px;
}

.yearname {
    display: flex;
    width: 200px;
    font-size: small;
    justify-content: space-between;
    /* background-color: darkorange; */
}

.companylocation {
    display: flex;
    width: 180px;
    height: 25px;
    /* align-items: center; */
    gap: 3px;
    /* justify-content: center; */
    /* background-color: aqua; */
}

.companylocation>p {
    font-size: 10px;
}

.locationlogo {
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: black; */
    font-size: 12px;
}

.btn:hover {
    color: #275DF5;
    border: 1px solid #275DF5;

}


/*  rightcompanydescripton  */
.rightcompanydescripton {
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: center;
    height: 100%;
    width: 40%;
    /* background-color: hotpink; */
}

.rightcompanydescriptonlogo {
    height: 80px;
    width: 80px;
    border-radius: 10px;

    overflow: hidden;
    /* background-color: aqua; */
}

.rightcompanydescriptonlogo>img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.rightcompanydescripton>a {
    font-size: 15px;
}

/*  company details button  */
.details {
    height: 30%;
    width: 100%;
    /* background-color: darkslategrey; */
    display: flex;
    align-items: center;
}

.openinginformation {
    display: flex;
    width: 60%;
    height: 100%;
    padding-left: 10px;
    justify-content: space-around;
    /* background-color: rgb(128, 0, 90); */
    align-items: center;
    /* justify-content: center; */
}

.openinginformation span {
    font-size: 12px;
    font-weight: 900;
}

.openinginformation p {
    font-size: 12px;

}

.detailbutton {
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
    /* background-color: yellow; */
    width: 50%;
    height: 100%;
    margin-left: 4vw;
    padding: 0 10px;
    gap: 3px;
}

.detailbutton>button {
    width: 130px;
    height: 40px;
    /* background-color: blue; */
    /* color: white; */
    padding: 10px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: 700;
}

.btn1 {
    background-color: white;
    border: 1px solid rgb(152, 175, 175);
    color: #275DF5;
}

.btn1:hover {
    /* color: #ffff; */
    border: 1px solid rgb(152, 175, 175);
    opacity: 0.8;
}

.btn2 {
    background-color: #275DF5;
    /* border: 1px solid rgb(56, 55, 55); */
    border: 1px solid rgb(152, 175, 175);
    color: #ffff;
}

.btn2:hover {
    /* color: #275DF5; */
    /* background-color: #ffff; */
    border: 1px solid rgb(152, 175, 175);
    opacity: 0.8;
}




