}

.reviewpost {
    display: flex;
    height: 30%;
    align-items: center;
    /* background-color: #FFF3DD; */
}

.reviewpostleft {
    height: 100%;
    width: 60%;
    font-size: 12px;
    display: flex;
    align-items: center;
    /* background-color: rgb(165, 143, 19); */
}

.reviewpostright {
    height: 100%;
    width: 40%;
    /* background-color: rgb(255, 0, 64); */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.reviewpostright>button {
    padding: 8px 16px;
    font-size: 14px;
    height: 40px;
    width: 80px;
    border: 1px solid #275DF5;
    background-color: #f0f0f0;
    /* Background color */
    color: #275DF5;
    /* Text color */
    border-radius: 4px;
    /* Rounded corners */
    cursor: pointer;
    /* Change cursor on hover */
    transition: background-color 0.3s ease;
    /* Smooth transition on hover */
}

.reviewpostrigh>button:hover {
    background-color: #592dbe;
}

.reviewpostright>p {
    font-size: x-small;
}

