    padding: 20px;
}

.servicedetail {
    height: 70%;
    width: 100%;
    /* background-color: #a36045; */

}

.servicelink {
    height: 20%;
    width: 100%;
    /* background-color: #592dbe; */
    display: flex;
    /* justify-content: center; */
    justify-content: space-between;
    /* align-items: center; */
}

.servicelink>p {
    font-size: 14px;
    font-weight: 700;
}

.servicelink>a {
