@media screen and (max-width: 640px) {

    .jobcontainer {
        /* background-color: yellow; */
        flex-direction: column;
        padding: 10px;
    }

    .leftContainer {
        /* background-color: green; */
        width: 100%;
        height: auto;
    }

    .rightContainer {
        width: 100%;
        padding: 5px;
    }

    .rightcompanydescripton>a {
        font-size: 10px;
    }

    .openinginformation {
        width: 100%;
    }

    .details {
        flex-direction: column;
    }

    .detailbutton {
        /* flex-direction: column; */
        width: 100%;
        gap: 5px;
    }

    .recommendedjobsdetail {
        height: 40%;
    }

    .recommendedjobs {
        height: auto;

    }

}


@media (min-width: 640px) and (max-width: 850px) {
    .jobcontainer {
        /* background-color: palegoldenrod; */
        padding: 10px;
    }

    .rightContainer {
        padding: 5px;
        width: 40%;
    }

    .recommendedjobs {
        height: auto;
        padding: 10px;
    }

    .detailbutton {
        flex-direction: column;
        gap: 5px;
        width: 40%;
    }

    .servicesection {
        height: 300px;
    }

}


@media (min-width:1200px) {
    .detailbutton {
        margin-left: 10vw;
    }
}




